/* eslint-env jest */
import React, { useContext, useCallback } from 'react';
import T from 'prop-types';
import { mount } from 'enzyme';
import {
    TelemetryContext,
    TelemetryContextProvider,
    MetricsCollector,
} from '@splunk/dashboard-telemetry';
// Telemetry is compiled against the current UDF version, so this version will be present in the expected payloads
import ENV from '../utils/environment';

export const sendEventMock = jest.fn();

export const createExpectedPayload = (data) => ({
    eventType: 'udf.telemetry',
    level: 'info',
    data: {
        udfVersion: ENV.version,
        metadata: {},
        ...data,
    },
});

export const expectedFlushPayload = createExpectedPayload({
    pageAction: 'test.flush',
});

class MetricsCollectorMock extends MetricsCollector {
    sendEvent = sendEventMock;
}

const FlushButton = () => {
    const telemetry = useContext(TelemetryContext);

    const handleClick = useCallback(() => {
        telemetry.flush({
            pageAction: 'test.flush',
        });
    }, [telemetry]);

    return (
        <button
            type="button"
            aria-label="flush"
            data-test="flush-button"
            onClick={handleClick}
        />
    );
};

const metricsCollectors = [new MetricsCollectorMock()];

// eslint-disable-next-line react/prop-types
const ContextWrapper = ({ children }) => (
    <TelemetryContextProvider metricsCollectors={metricsCollectors}>
        {children}
        <FlushButton />
    </TelemetryContextProvider>
);

ContextWrapper.propTypes = {
    children: T.any,
};

export const mountWithTelemetry = (Component) => {
    return mount(Component, {
        wrappingComponent: ContextWrapper,
    });
};
