import React, { useState, useMemo, useCallback } from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import Select from '@splunk/react-ui/Select';
import SplunkThemeProvider from '@splunk/themes/SplunkThemeProvider';

const ParentContainer = styled.div.attrs({ 'data-test': 'parent-container' })`
    width: 100%;
    height: 100vh;
    background-color: ${(props) => props.theme[props.bgColorKey]};
    color: ${(props) => props.theme[props.textColorKey]};
`;

const themeToVariant = {
    enterprise: { colorScheme: 'light', family: 'enterprise' },
    enterpriseDark: { colorScheme: 'dark', family: 'enterprise' },
    scp: { colorScheme: 'dark', family: 'prisma' },
};

const themeDecoratorGenerator = (defaultBackgroundColor) => (themes) => {
    const Wrapped = ({
        storyFn,
        defaultTheme,
        showThemeSelector,
        backgroundColorKey,
        textColorKey,
    }) => {
        const [theme, setTheme] = useState(defaultTheme);
        const handleThemeChange = useCallback(
            (e, { value }) => setTheme(value),
            []
        );

        // Wrap the story function in theme provider
        const storyWithTheme = useMemo(
            () => (
                <SplunkThemeProvider
                    additionalThemeProperties={themes[theme]}
                    {...themeToVariant[theme]}
                >
                    <ParentContainer
                        bgColorKey={backgroundColorKey}
                        textColorKey={textColorKey}
                    >
                        {storyFn()}
                    </ParentContainer>
                </SplunkThemeProvider>
            ),
            [storyFn, theme, backgroundColorKey, textColorKey]
        );
        // Allow the selection of theme within storybooks
        const ThemeSelector = (
            <Select value={theme} onChange={handleThemeChange}>
                <Select.Option label="Enterprise" value="enterprise" />
                <Select.Option label="Enterprise Dark" value="enterpriseDark" />
                <Select.Option label="SCP" value="scp" />
            </Select>
        );

        return (
            <>
                {showThemeSelector && <div>{ThemeSelector}</div>}
                {storyWithTheme}
            </>
        );
    };

    Wrapped.propTypes = {
        storyFn: T.func.isRequired,
        defaultTheme: T.string,
        showThemeSelector: T.bool,
        backgroundColorKey: T.string,
        textColorKey: T.string,
    };

    Wrapped.defaultProps = {
        defaultTheme: 'enterprise',
        showThemeSelector: false,
        backgroundColorKey: defaultBackgroundColor,
        textColorKey: 'headerTextColor',
    };

    return Wrapped;
};

export const sidebarThemeDecorator = themeDecoratorGenerator(
    'panelBackgroundColor'
);
export const canvasThemeDecorator = themeDecoratorGenerator(
    'dashboardBackgroundColor'
);

export default sidebarThemeDecorator;
