import React from 'react';
import T from 'prop-types';
import { mount } from 'enzyme';
import SplunkThemeProvider from '@splunk/themes/SplunkThemeProvider';

const ThemeWrapper = ({ themeProperties, children }) => (
    <SplunkThemeProvider {...themeProperties}>{children}</SplunkThemeProvider>
);

ThemeWrapper.propTypes = {
    children: T.any,
    themeProperties: T.object,
};

export default (
    Component,
    themeProperties = {
        colorScheme: 'dark',
        mode: 'comfortable',
        family: 'prisma',
    }
) =>
    mount(Component, {
        wrappingComponent: ThemeWrapper,
        wrappingComponentProps: { themeProperties },
    });
